import React, { useState, useEffect } from 'react';
import * as musicMetadata from 'music-metadata-browser';


const EachTone = ({ songDetails, playSong, updatePlayer }) => {
  const handleClick = () => {
    playSong(songDetails.url);
    updatePlayer(songDetails);
  };
  // console.log(`../Files/covers/${songDetails.imageUrl}`)

  return (
    <div className="song" onClick={handleClick}>
      <div className="song-img">
      <img 
  src={songDetails.imageUrl 
    ? `${process.env.PUBLIC_URL}/covers/${songDetails.imageUrl}` 
    : 'https://images.unsplash.com/photo-1521133573892-e44906baee46?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=934&q=80'
  } 
  alt="album cover" 
/>
  </div>
      <div className="song-detail">
        <div className="song-name">{songDetails.songName}</div>
        <div className="artist">{songDetails.artist}</div>
        <div className="album">{songDetails.album}</div>
      </div>
    </div>
  );
};
export default EachTone;
