import React, { useState, useEffect } from 'react';
import * as musicMetadata from 'music-metadata-browser';
import JSZip from 'jszip'; // Import JSZip for creating zip files

const GenerateJsonData = () => {
  const [jsonData, setJsonData] = useState(null);

  // Function to import all files from a specific directory
  const importAll = (requireContext) => {
    return requireContext.keys().map(requireContext);
  };

  const extractSongDetails = async (files) => {
    const playlist = [];

    for (const file of files) {
      try {
        const response = await fetch(file);
        const blob = await response.blob();
        const metadata = await musicMetadata.parseBlob(blob);

        const { common } = metadata;
        const songDetails = {
          songName: common.title || 'Unknown Title',
          artist: common.artist || 'Unknown Artist',
          album: common.album || 'Unknown Album',
          url: file,
          imageUrl: null // Placeholder for image URL
        };

        // Handle album art
        if (common.picture && common.picture.length > 0) {
          const imageData = common.picture[0].data;
          songDetails.imageUrl = `${songDetails.songName.replace(/[^a-zA-Z0-9]/g, '_')}.jpg`; // Sanitize filename
          playlist.push({ ...songDetails, imageBlob: imageData }); // Store image data for zipping later
        } else {
          // Default image if no album art is found
          songDetails.imageUrl = '';
          playlist.push(songDetails);
        }
      } catch (error) {
        console.error(`Error processing file ${file}:`, error);
      }
    }

    return playlist;
  };

  const generateSongDataJson = async () => {
    try {
      // Import files from different playlists
      const poisonFiles = importAll(require.context('../Files/Poison/', false, /\.mp3$|\.m4a$/));
      const myFiles = importAll(require.context('../Files/My/', false, /\.mp3$|\.m4a$/));
      const teluguFiles = importAll(require.context('../Files/Telugu/', false, /\.mp3$|\.m4a$/));

      // Extract song details for each playlist
      const poisonSongs = await extractSongDetails(poisonFiles);
      const mySongs = await extractSongDetails(myFiles);
      const teluguSongs = await extractSongDetails(teluguFiles);

      // Combine all songs into a single JSON object
      const combinedSongsData = {
        Poison: poisonSongs.map(({ imageBlob, ...rest }) => rest),
        My: mySongs.map(({ imageBlob, ...rest }) => rest),
        Telugu: teluguSongs.map(({ imageBlob, ...rest }) => rest),
      };

      // Download the combined JSON file
      downloadJsonFile(combinedSongsData, 'songData.json');

      console.log("Combined song data JSON file generated and downloaded.");

      // Create a zip file for all images
      await createZipForImages(poisonSongs.concat(mySongs, teluguSongs)); // Combine all songs for zipping

    } catch (error) {
      console.error("Error generating song data:", error);
    }
  };

  const downloadJsonFile = (data, filename) => {
    const blob = new Blob([JSON.stringify(data, null, 2)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    a.click();
    URL.revokeObjectURL(url);
  };

  const createZipForImages = async (songs) => {
    const zip = new JSZip();

    songs.forEach((song) => {
      if (song.imageBlob) {
        // Add image blob to the zip with sanitized filename
        zip.file(`${song.songName.replace(/[^a-zA-Z0-9]/g, '_')}.jpg`, song.imageBlob);
      }
    });

    // Generate the zip file and trigger download
    zip.generateAsync({ type: "blob" }).then(function(content) {
      // Force download of the zip file
      const url = URL.createObjectURL(content);
      const a = document.createElement('a');
      a.href = url;
      a.download = 'album_covers.zip'; // Name of the zip file
      document.body.appendChild(a); 
      a.click(); 
      document.body.removeChild(a); 
      URL.revokeObjectURL(url); 
      
      console.log("Album covers zipped and downloaded.");
    });
  };

  useEffect(() => {
    generateSongDataJson();
  }, []);

  return (
    <div>
      <h1>Generate JSON Data</h1>
      {jsonData ? (
        <pre>{jsonData}</pre>
      ) : (
        <p>Generating JSON data...</p>
      )}
    </div>
  );
};

export default GenerateJsonData;